.navigation {
    position: relative;
    display: flex;
    align-items: center;
    top: -6.5rem;   
    margin: 0;
    padding: 0;

    .classic {
        display: flex;
        flex-direction: row;
        align-items: center;
        padding-inline-start: 0;
        .link {
            
            .button {
                font-family: lobster;
                color: #6b376b;
                filter: drop-shadow(0 0.1rem 0.1rem #f74cb2);
                font-size: 1.3rem;
                border: none;
                background: none;
                height: 2rem;
                cursor: pointer;
                transition: transform 0.3s ease;

                &:hover {
                    color: #faf2f1;
                    filter: drop-shadow(0 0.1rem 0.1rem #503846);
                    transform: translateY(-0.2rem);
                }
            }
            .icon {
                font-size: 1.5rem;
            }
            .letters {
            } 
        }
    }
}