@import url('https://fonts.googleapis.com/css2?family=Charm&family=Lobster&family=Merienda&display=swap');

* {
  box-sizing: border-box;

  body {
    margin: 0;
    padding: 0;
    overflow-x: hidden;

    header {
      background: #f5d8da;
      height: 11rem;
      display: flex;
      flex-direction: column;
      filter: drop-shadow(0 0 0.1rem #83256e);
      align-items: center;
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      z-index: 2;

      .flowers {
        filter: drop-shadow(0 0 0.033rem #83256e);
        position: absolute;
        height: 9rem;

        &.left {
          left: 0;
          transform: scale(-1);
        }

        &.right {
          right: 0;
          top: 2rem;
        }
      }
    }

    main {
      position: relative;
      margin-top: 14rem;
      z-index: 0;
    }

    footer {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      position: relative;
      z-index: 1;
      filter: drop-shadow(0 0.1rem 0.1rem #868285);
      background: linear-gradient(to right, #79526e, #4d364d, #79526e);
    }
  }
}
