.salon {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  width: 80vw;
  text-align: center;
  font-family: "charm";
  font-size: 1.2rem;

  &:nth-child(odd) {
    flex: 1;
  }

  &:nth-child(even) {
    flex: 2;
  }

  .text {
    padding: 1rem;
    width: 33vw;
    line-height: 1.2rem;
  }

  .photo {
    filter: drop-shadow(0 0.1rem 0.1rem #6e5c6b);
    width: 33vw;
    z-index: -1;
  }

  .owner {
    filter: drop-shadow(0 0.1rem 0.1rem #6e5c6b);
    position: absolute;
    right: -7vw;
    margin-top: 42.9%;
    width: 45vw;
    z-index: -1;
  }
}
