.services {
  margin-top: 12rem;
  padding: 2rem;

  .intro {
    font-family: charm;
    font-size: 1.6rem;
    text-shadow: 0 0.1rem 0.1rem #ee92f1;
    text-align: center;
    padding: 2rem;
    text-transform: uppercase;
  }

  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #f8e9f7;
    padding: 1rem;
    filter: drop-shadow(0 0.1rem 0.1rem #755873);

    &.top {
      border-top-right-radius: 1rem;
      border-top-left-radius: 1rem;
    }

    &.bottom {
      border-bottom-right-radius: 1rem;
      border-bottom-left-radius: 1rem;
    }

    .title {
      padding: 1rem 2vw 0rem 0vw;
      border-right: solid 2rem #92307a;
      border-top-right-radius: 50%;
      width: 40vw;
      text-decoration: underline;
      margin-left: 15vw;
      text-align: end;
      font-size: 1.5rem;
      font-family: "Merienda";
    }

    .text {
      line-height: 1.4rem;
      font-family: merienda;
    }
  }
}
