.items {
  margin-top: 1rem;
  display: flex;
  width: 100vw;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  color: white;
  font-family: "Lobster";

  .company {
    display: flex;
    flex-direction: column;

    .title {
      text-align: center;
      text-decoration: underline;
    }

    .button {
      cursor: pointer;
      color: white;
      font-size: 1rem;
      font-family: "Lobster";
      background: none;
      border: none;
      margin: 0.4rem;
      box-shadow: 0 0 0.1rem white;
      transition: scale 0.5s;

      &.facebook,
      &.instagram {
        box-shadow: none;
        font-size: 1.5rem;
      }

      &:hover {
        filter: drop-shadow(0 0.1rem 0.1rem #887d84);
        scale: 1.1;
      }
    }
  }

  .table {
    text-align: center;
  }
}

.copyright {
  color: white;
  text-align: center;
  font-size: 1rem;
  font-family: "Lobster";
}
