.welcome {
  display: flex;
  flex-direction: row;
  background-color: #bbb5b6;
  width: 100vw;
  height: 20rem;
  font-family: 'Charm', cursive;

  .description {
    filter: drop-shadow(0 0.1rem 0.1rem #877288);
    background: linear-gradient(90deg, transparent, white);
    width: 100%;

    .text {
      font-size: 1.1rem;
      padding: 0rem 1vw 0rem 1vw;
      line-height: 1.35rem;
      max-width: 30em;
    }
  }

  .background {
    height: 20rem;
  }

  .contact {
    left: 35vw;
    margin-top: 24rem;
    position: absolute;
    height: 10rem;
    width: 10rem;
    border: none;
    background: radial-gradient(circle, pink, white);
    border-radius: 50%;
    cursor: pointer;
    filter: drop-shadow(0 0.1rem 0.1rem #f277f8);
    transition: scale 0.5s;

    &:hover {
        scale: 1.05;
        filter: drop-shadow(0 0.1rem 0.1rem #f8f2f8);
    }

    .art {
      height: 10rem;
    }

    .text {
      font-size: 2rem;
      font-family: lobster;
    }
  }
}
