.arkana {
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: -1rem 0 1rem black;
  font-family: charm;

  .brand {
    margin-top: 3rem;
    width: 60vw;
    border-radius: 0.5rem;
    filter: drop-shadow(0 0.1rem 0.1rem #6e5c6b);
  }

  .container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    text-align: center;

    .cosmetic {
      width: 20vw;
      border-radius: 1rem;
      filter: drop-shadow(0 0.1rem 0.1rem #8f8a8e);
    }

    .subcontainer {
      margin-bottom: 3rem;
      .title {
      }
      .text {
        background: #f8c7f1;
        padding: 0.5rem;
        border-radius: 0.4rem;
      }
    }
  }
}
